.aboutcontainer{
     width:100%;
    min-height: 100vh;

}

.aboutcontainer > .title > h1{
    color:red;
    font-size: 50px;
    margin-top:20px;
    text-align: center;

}
.aboutcontainer > .allcontent{
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    
}
.aboutcontainer .allcontent >.leftcontent{
    flex:50%;
    display: flex;
    align-items: center;
}
.aboutcontainer .allcontent >.leftcontent>img{
    width:400px;
    height:400px;
    border-radius: 50%;
    margin-left: 30px;
    border:5px solid blueviolet;
    margin-top: 40px;
}
.aboutcontainer .allcontent >.leftcontent>img:hover{
    transform: scale(1.1);
    
}
.aboutcontainer .allcontent >.rightcontent{
    flex:50%;
    font-family: monospace;
     font-size: 25px;

    
}