.productcontainer{
    width:100%;
    height:100vh;
    
    
}
 .menu{
    text-align: center;
    font-size: 30px;
    color:brown;
    margin-top: 10px;

}
.productcontainer > .cardcontainer{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    width:100%;
    height:auto;
}
.productcontainer > .cardcontainer > .card{
    width:350px;
    height:auto;
    margin:20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;

}
.productcontainer >.cardcontainer >.card > .card-body >img{
width:100%;
height:300px;
object-fit: cover;

}
.productcontainer >.cardcontainer >.card > .card-body >img:hover{
   transform: rotate(360deg);
}
.cardtext{
    text-align: center;
    padding:20px;
}
.cardtext > p{
    color:red;
}
 .btn1{
    padding:15px;
    border:none;
    outline:none;
    background-color:chartreuse;
    color:white;
    font-weight: bold;
    margin-top:10px;
    border-radius: 10px;
}