*
{
    padding:0;
    margin:0;
    box-sizing: border-box;
}
.navbar
{
    
    width:100%;
    height:80px;
    display:flex;
    align-items: center;
    flex-direction: row;
    background-color: #000;
    top:0;
    position: sticky;
}
.navbar .leftside
{
    flex:40%;
    height:100%;
    display:flex;
    align-items: center;
    padding-left: 100px;
}
.navbar .leftside img{
    width:70px;
    border-radius: 50%;
    
    
}
.navbar .Rightside{
    display:flex;
    align-items: center;
    flex:60%;
    width:100%;
    height:100%;
   

}
.navbar  a{
    color:white;
    text-decoration: none;
    font-size: 20px;
    margin: 30px;
    padding-left: 30px;
}
.navbar  a:hover{
    background-color: orange;
    padding:10px 20px;
    border-radius: 10px;
    transition: 1s;
}






