*
{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.formcontainer{
    width:100%;
    height:100vh;
    display:flex;
    position: relative;
}
.imggallery{
    position: relative;
    width:50%;
    height:90%;

}
.imggallery:before{
    content:"";
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background:linear-gradient(225deg,#e91e63,#03a9f4);
    z-index: 1;
    mix-blend-mode: screen;
}
.imggallery > img{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:90%;
    object-fit: cover;
}
.contactbox{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height:90%;
    width:50%;
    background-image: url("https://highpng.com/wp-content/uploads/2021/05/poster-dough-four-pizza-free-download-from-highpng-download-more-free-png-images.png");
      background-repeat: no-repeat;
    background-position:center;
    background-size: cover;
   
    
}


.contactbox h2{
    color:rgb(175, 29, 29);
}
form{
    display:flex;
    flex-direction: column;
    padding:20px;
    margin-top: 10px;
    text-align: center;
    background: rgb(190, 173, 150);
    border-radius: 10px;
    padding-bottom: 20px;

}
form > .form-field{
    margin:20px;
    padding:10px;
    border-radius: 10px;
    border:none;
    outline: none;

}
.btn2{
    width:150px;
    margin-left: 35px;
    padding:10px;
    background: blue;
    border:none;
    outline:none;
    color:white;
    border-radius: 10px;
    margin-top: 10px;
}

.success{
    background: crimson;
    width:200px;
    color:white;
    padding:10px 5px;
    margin-left: 10px;
    border-radius: 4px;
    text-align: center;
}
span{
    color:red;
}











