.footercontainer{
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    width:100%;
    height:30vh;
    background: #000;
    color:white;
    margin-top: -65px;
   

    
   
    
}
.footercontainer .leftfooter{
    display:flex;
    flex-direction: column;
    align-items: center;

}
.leftfooter >a{
    color:white;
    text-decoration: none;
    font-size: 22px;
    margin:10px;
}
.leftfooter >a:hover{
    border-bottom: 3px solid red;
}
.rightfooter{
    display:flex;
    flex-direction: column;
}
.rightfooter > ul{
    list-style: none;
}
.rightfooter > ul > li{
    color:red;
    font-size: 18px;
    margin:20px 0px;
}
.rightfooter > ul > li:hover{
    color:blue;
    transform: rotate(360deg);
    transition: 2s;
}