.container{
    height:60vh;
    width:100%;
    position: relative;
}

video{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    z-index: -1;
    
}


.container > .content{
padding-top:15%;
padding-left: 20%;;
}

.container > .content > h1{
    color:white;
    font-size: 70px;
    border-bottom: 3px solid red;
    max-width: 500px;
}
.container >.content > h2{
    color:white;
    margin-top: 15px;
    font-size: 30px;
    font-weight: bold;
}

.container >.content > button{
    border:none;
    outline: none;
    background-color:rgb(28, 60, 156);
    color:white;
    padding:15px;
    margin-top: 20px;
    border-radius: 10px;
    cursor: pointer;
    width:150px;
}
.container >.content > button:hover{
    background-color: seagreen;
    transition: ease-in-out;
    letter-spacing: 3px;
}



















